export default{
	data(){
		return{
			typeStus: 0, //类型状态
			langch:{
				langzw:'中文',
				langyw:'英文',
				baocun:'保存',
				yuyan:'语言切换'
			},
			langen:{
				langzw:'Chinese',
				langyw:'English',
				baocun:'Preservation',
				yuyan:'Language switching'
			},
			baseUrl:this.$common.baseUrl,
			avatar:uni.getStorageSync('userinfo').avatar,
			username:uni.getStorageSync('userinfo').username,
			langtype:''
		}
	},
	onShow() {
		this.langtype = uni.getStorageSync('lang')?uni.getStorageSync('lang'):'CH'
		if(this.langtype=='CH'){
			this.typeStus = 0;
		}else{
			this.typeStus = 1;
		}
	},
	methods:{
		//选择类型
		selectType(num) {
			this.typeStus = num;
			if(num==0){
				// uni.setStorageSync('lang','CH');
				this.langtype = 'CH';
			}else{
				// uni.setStorageSync('lang','EN');
				this.langtype = 'EN';
			}
			
		},
		back(){
			 uni.navigateBack({
				delta:1
			 })
		},
		gosub(){
			var num = this.typeStus ;
			if(num==0){
				uni.setStorageSync('lang','CH');
			}else{
				uni.setStorageSync('lang','EN');
			}
			this.$api.msg('设置成功');
		}
	}
}
